import React from "react"

import "./style.css"
import { useState } from "react"
import { useEffect } from "react"

export default ({ title }) => {
    
    const [view, setView] = useState("landing")

    useEffect(() => {
        console.log(view)
        view && document.getElementById(view) && document.getElementById(view).scrollIntoView({ behavior: 'smooth', block: 'center' });
    },
     [view])
    
    return(
        <div className="nav" id="navbar">
            <nav>
                <h1 className="logo">{title}</h1>
                <ul>
                    <a onClick={ () => setView("landing") }><li>HELLO</li></a>
                    <a onClick={ () => setView("about-me") }><li>ABOUT ME</li></a>
                    <a onClick={ () => setView("project") }><li>PROJECTS</li></a>
                </ul>
            </nav>
        </div>
    )
}